  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
  box-sizing: border;
  background-color: rgb(201, 201, 201);
}

main {
  min-height: 100vh;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: lightgrey;
}

.skip-link {
  position: absolute;
  top: -70px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
  display: inline-block;
  min-height: 44px;
  min-width: 44px;
}

.skip-link:focus {
  top: 0;
}

.nametag {
  margin-left: 32px;
  font-size: 16px;
}

h1 a,
.nav-links-mob li a {
  text-decoration: none;
  color: black;
  display: inline-block;
  min-height: 44px;
  min-width: 44px;
}

.nav-links-mob li a {
  margin-top: 12px;
}

.navbarmob {
  display: flex;
  align-items: center;
  margin-right: 32px;
  font-size: 16px;
}

.hamburger {
  display: none;
}

.nav-links-mob {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbarmob ul {
  display: flex;
  gap: 40px;
}

.nav-links-mob li {
  display: inline;
  padding: 16px;
  min-height: 44px;
  min-width: 44px;
}

.nav-links-mob a:hover {
  opacity: 0.7;
}

h2 a{
  display: inline-block;
  min-height: 22px;
  min-width: 22px;
}

.hero {
  height: 400px;
  overflow: hidden;
  position: relative;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero img {
  object-fit: cover;
  max-width: 100%;
  width: 100%;
  height: 100%;
}

.slogan {
  display: flex;
  justify-content: center;
  font-size: 40px;
  font-family: 'Lobster', cursive;
}

.restaurant-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 25px;
}

.restaurant-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  padding: 0 20px;
  margin-top: 20px;
}


#restaurant-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.restaurant {
  background-color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
  text-align: center;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
}

.restaurant:hover {
  transition: all .2s ease-in-out;
  top: -4px;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 15px;
}

.detail-title {
  text-align: center;
  font-size: 24px;
}

.detail-header {
  display: flex;
  flex-basis: 1;
  justify-content: center;
  align-items: start;
  gap: 20px;
  margin-bottom: 24px;
}
.resto-description {
  font-size: 17px;
  line-height: 1.5;
}


.detail-header img {
  width: 100%;
  height: 350px;
  border-radius: 10px
}

.detail-menu-wrapper {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 20px;
}

.list-group-item {
  display: inline-block;
  text-align: center;
  background-color: #F3EEEA;
  padding: 10px;
  border-radius: 10px;
}
.restaurant img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  background-color: #5c5c5c;
  color: white;
}

.footer aside {
  text-align: center;
}

.footer aside p {
  margin: 0;
  font-size: 1rem;
}

.footer aside p a {
  text-decoration: none;
  color: inherit;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

.fav-header{
  text-align: center;
  font-size: 28px;
}

.detail-img {
  width: 100%;
  height: 300px;
}

.info-resto {
  display: flex;
}

.info-resto p {
  font-size: 14px;
}

.resto-desc {
  font-size: 14px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  font-weight: 500;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 16px 15px;
  font-size: 14px;
  line-height: 1.5em;
  color: black;
  text-align: left;
}

.resto-image-wrapper {
  position: relative;;
}

.resto-image-wrapper p {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 20px;
  background-color: rgba(0,0,0,.6);
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  cursor: default;
  margin: 0
}

.resto-menu-wrapper {
  /* display: flex;
  justify-content: center;
  align-items: start;
  gap: 20px; */
  padding: 20px;
}

.resto-menu-wrapper h3 {
  font-size: 16px;
}

.resto-menu-wrapper li {
  font-size: 14px;
}

.resto-review-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.resto-name a {
  text-decoration: none;
  color: black;
  font-size: 20px;
}

.resto-name a:hover {
  opacity: 0.7;
}

.review-container {
  width: 100%;
  max-width: 500px;
  margin-top: 20px;
  padding: 12px;
  border: 1px solid black;
  border-radius: 4px;
}

.review-container h2 {
  font-size: 16px;
}

.review-container p {
  font-size: 14px;
}

.menu-title{
  font-size: 28px;
}

.title {
  text-align: center;
}

@media screen and (min-width: 568px) {
  .detail-img {
    width: 600px;
    height: auto;
  }

  /* .resto-description {
    width: 500px;
  } */

  .resto-review-wrapper {
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
